<template>
  <b-container id="client">
    <b-row>
      <b-col>
        <div class="container-title">
          <div class="title-background"></div>
          <div class="title-text">
            <span class="span-lighter">OUR</span>&nbsp;
            <span class="span-bolder">CLIENTS</span>
          </div>
        </div>
      </b-col>
    </b-row>
    <b-row class="pt-4 pb-2 pr-2 pl-2">
      <p id="sub-title"> We are so thankful for the trust from our clients. </p>
    </b-row>
    <b-row class="justify-content-center" style="margin-left:1rem; margin-right:1rem;">
      <b-col
        v-for="data in clients.slice(0, clients.length)"
        :key="data.id"
        class="text-center"
        cols="auto"
      >
        <b-img class="image" center fluid :src="url_image + data.logo" :title="data.name"></b-img>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
export default {
  name: "Clients",
  data() {
    return {
      title: "Trusted By",
      clients: [],
      url_image: process.env.VUE_APP_API_URL + "/images/portofolios/"
    };
  },
  mounted() {
    this.axios
      .get(process.env.VUE_APP_API_URL + "/api/v1/clients")
      .then(response => {
        this.clients = response.data.filter(function(data) {
          return data.publish;
        });
      });
  }
};
</script>

