<template>
  <div class="cont">
    <img id="img-bg" src="../assets/FooterBg.png" />
    <div class="footer-cont">
      <img id="logo-navbar-sirkel" class="mb-4" src="../assets/logo-sirkel-aja.svg" alt="logo sirkel"/>
      <p id="company">{{ companyName }}</p>
      <p id="address" class="mb-4">{{ companyAddress1 }}</p>
      <div id="socmed-cont">
        <div
          class="socmed-subcont"
          v-for="(socmed_item,index) in socmed"
          :key="socmed_item.id"
        >
          <a target="_blank" rel="noopener noreferrer" :href="socmed_item.information">
            <b-img
              :id="socmed_item.value"
              class="image_sosmed "
              left
              fluid
              :src="api_url + '/images/icons/' + socmed_item.value"
            ></b-img>
            <b-popover
              :target="socmed_item.value"
              triggers="hover"
              placement="top"
            >{{popoverMessage[index]}}</b-popover>
          </a>
        </div>
      </div>
    </div>
    <div id="link-cont">
      <a href="/"> Home </a>
      <!-- <a href="/demo"> Demo </a> -->
      <a href="/request"> Offer </a>
    </div>
    <div class="to-top-cont">
      <img @click="scrollToTop" src="../assets/go_to_top_btn.png"/>
      <p>Go to top</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "Footer",
  data() {
    return {
      companyName: "",
      companyAddress1: "",
      reachUsText: "",
      socmed: [],
      api_url: process.env.VUE_APP_API_URL,
      popoverMessage: [
        "open instagram sirkel.id",
        "mail to contact@sirkel.id",
        "chat to 081322055655"
      ]
    };
  },
  mounted() {
    this.axios
      .get(process.env.VUE_APP_API_URL + "/api/v1/options_sirkel")
      .then(response => {
        for (let i = 0; i < response.data.length; i++) {
          if (response.data[i].name == "company_name") {
            this.companyName = response.data[i].value;
          } else if (response.data[i].name == "address_1") {
            this.companyAddress1 = response.data[i].value;
          } else if (response.data[i].name == "reach_us") {
            this.reachUsText = response.data[i].value;
          } else if (response.data[i].type == "socmed") {
            this.socmed.push(response.data[i]);
          }
        }
      });
  },
  methods:{
    scrollToTop() {
      window.scrollTo(0,0);
    }
  }
    
};
</script>

