<template>
  <transition
    enter-active-class="animate__animated animate__fadeIn animate__faster"
    leave-active-class="animate__animated animate__fadeOut animate__faster"
    mode="out-in"
  >
    <b-container class="design-sprint" v-if="!isModalVisible">
      <img class="img-cloud2" :src="require('@/assets/awan2.svg')" alt="">
      <div id="go-top"></div>
      <b-row>
        <b-col>
          <div class="container">
            <p class="title-text">
              <span class="design-sprint-text">DESIGN SPRINT</span>&nbsp;
              <span class="step">STEPS</span>
            </p>
          </div>
        </b-col>
      </b-row>
      <b-row>
        <b-col>
          <b-col>
            <div id="description" class="text-center">
              <p>
                We help you build and test your idea within
                <span class="span-bolder">five days</span>. Learn insights from
                your customer with
                <span class="span-bolder">less cost and time.</span>
              </p>
            </div>
          </b-col>
        </b-col>
      </b-row>

      <b-row class="ds-content">
        <b-col style="margin-top: 20px; margin-bottom: 20px;" v-for="(data, index) in data_design_sprint" v-bind:key="data.id">
          <div class="tooltip-f">
            <div class="ds-subcontent">
              <div
                id="test1"
                v-on:click="
                  click_image(index, data.title, data.description, data.imgURL)
                "
              >
                <b-img
                  class="ds-image"
                  :id="index"
                  center
                  :src="base_img_url + data.imgURL"
                ></b-img>
                <div>
                  <div class="numbering-step">{{ index + 1 }}</div>
                  <div class="capt-image">{{ data.title }}</div>
                </div>
              </div>

              <p v-if="$mq == 'sm'" id="ds-desc">{{ data.description }}</p>
            </div>
            <div class="vl"></div>
            <span :style="listItemStyle(index)" class="tooltiptext">{{
              data.description
            }}</span>
          </div>
        </b-col>
      </b-row>
      <b-row>
        <b-col>
          <p class="read-details">Hover to read the details</p>
        </b-col>
      </b-row>
    </b-container>
  </transition>
</template>

<script>
import axios from "axios";

export default {
  name: "DesignSprint",
  components: {},
  data() {
    return {
      isModalVisible: false,
      data_design_sprint: [],
      index: -1,
      title: "",
      description: "",
      base_img_url: "",
      render_design_element: 0,
      design_sprint_title: "Sirkel Design Sprint",
      design_sprint_description:
        "We help you build and test your idea within five days. Learn insights from your customer with less cost and time.",
      imgUrl: "",
    };
  },
  mounted() {
    axios
      .get(process.env.VUE_APP_API_URL + "/api/v1/designsprint")
      .then((response) => {
        this.data_design_sprint = response.data;
        this.base_img_url =
          process.env.VUE_APP_API_URL + "/images/designsprint/";
      });
  },

  methods: {
    listItemStyle: function(i) {
      var style = {};
      if (i === 0) {
        style.marginLeft = "-4vw";
      } else if (i === 1) {
        style.marginLeft = "-22vw";
      } else if (i === 2) {
        style.marginLeft = "-40vw";
      } else if (i === 3) {
        style.marginLeft = "-58vw";
      } else if (i === 4) {
        style.marginLeft = "-76vw";
      } else {
        style.marginLeft = "-94vw";
      }

      return style;
    },
  },
};
</script>

