<template>
  <div class="ds-brief">
    <img class="img-cloud" :src="require('@/assets/awan1.svg')" alt="">
    <b-container>
      <b-row class="py-3">
        <b-col md="6">
          <p class="title-text">
            <span class="what-is">What is</span>&nbsp;
            <span class="text-bold-own">Design Sprint?</span>
          </p>
          <p class="brief-text desc">{{ brief_text }}</p>
          <!-- <b-button type="submit" variant="primary" class="btn-see-demo"
            >SEE DEMO</b-button
          > -->
        </b-col>
        <b-col md="6">
          <video class="video" controls :src="video_url"></video>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
export default {
  name: "DesignSprintBrief",
  data() {
    return {
      brief_title: "hat is Design Sprint?",
      brief_text:
        "A design sprint is a time-constrained, five-phase process that uses design thinking with the aim of reducing the risk when bringing a new product, service or a feature to the market. The process aims to help teams to clearly define goals, validating assumptions and deciding on a product roadmap before starting development.",
      api_url: process.env.VUE_APP_API_URL,
      video_url: "",
    };
  },
  mounted() {
    this.axios
      .get(process.env.VUE_APP_API_URL + "/api/v1/options_sirkel")
      .then((response) => {
        for (let i = 0; i < response.data.length; i++) {
          if (response.data[i].name == "design_sprint_video") {
            this.video_url =
              this.api_url +
              "/storage/gif/short-profile/" +
              response.data[i].value;
            break;
          }
        }
      });
  },
};
</script>

