<template>
  <div>
    <div class="banner-chat">
      <transition
        enter-active-class="animate__animated animate__fadeIn animate__faster"
        leave-active-class="animate__animated animate__fadeOut animate__faster"
        mode="out-in"
      >
        <img key="close-button" @click="show = false" v-if="show" src="../assets/CloseButton.svg" />
        <img key="chat-button" @click="show = true; send_success = false;" v-else src="../assets/ChatButton.svg" />
      </transition>
    </div>
    <transition
      enter-active-class="animate__animated animate__fadeInUp"
      leave-active-class="animate__animated animate__fadeOutDown"
      mode="out-in"
    >
      <div key="suggestion-form" class="chat-form" v-if="show & !send_success">
        <div class="close-button">
          <button
            @click="show = false"
            type="button"
            class="close"
            data-dismiss="modal"
            aria-label="Close"
          >
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <p class="text-gradient text-center"><b>Have a question? Leave us a message!</b></p>
        <b-form @submit="onSubmit">
          <span class="label">
            <b>Your Name</b> <span class="text-secondary">(optional)</span>
          </span>
          <br />
          <b-form-input
            v-model="form.name"
            id="name-input"
            type="text"
            placeholder="Enter your name or institution name"
          ></b-form-input>
          <span class="label">
            <b>Email</b>
          </span>
          <br />
          <b-form-input
            v-model="form.email"
            id="email-input"
            type="email"
            required
            placeholder="Make sure it’s an active one!"
          ></b-form-input>
          <span class="label">
            <b>How can we help you?</b>
          </span>
          <br />
          <b-form-textarea
            v-model="form.message"
            id="message-input"
            type="textarea"
            rows="5"
            required
            placeholder="Tell us your problem here"
          ></b-form-textarea>
          <div class="text-right">
            <b-button type="submit" variant="primary" class="btn-submit">DONE</b-button>
          </div>
        </b-form>
      </div>
      <div key="success" class="success-form" v-if="send_success">
        <p class="text-gradient text-center"><b>Sent. We’ll reach you soon by email!</b></p>
        <div class="text-center"><b-button type="submit" @click="send_success = false" variant="primary" class="btn-submit">OKAY!</b-button></div>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  data() {
    return {
      show: false,
      form: {
        name: "",
        email: "",
        message: ""
      },
      send_success: false
    };
  },
  methods: {
    onSubmit(evt) {
      evt.preventDefault();
      var name = this.form.name == '' ? '-' : this.form.name;

      this.axios
        .post(process.env.VUE_APP_API_URL + "/api/v1/suggestion", {
          name: name,
          email: this.form.email,
          message: this.form.message
        })
        .then(response => {
          if (response.data.msg == "Suggestion created") {
            this.send_success = true;
          } else {
            alert("Your message failed to send. Try again later.");
          }
        });
      this.form.name = "";
      this.form.email = "";
      this.form.message = "";
      this.show = false;
    }
  }
};
</script>

