import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
//import Request from '../views/Request.vue'
// import Register from '../views/Register.vue'
//import Demo from '../views/Demo.vue'

Vue.use(VueRouter)

const routes = [{
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/request',
    name: 'Request',
    component: () => import(/* webpackChunkName: "request" */ '../views/Request.vue')
  },
  {
    path: '/register',
    name: 'Register',
    component: () => import(/* webpackChunkName: "Register" */ '../views/Register.vue'),
    props: true,
    meta: {
      auth: "guest"
    }
  },
  {
    path: '/demo',
    name: 'Demo',
    component: () => import(/* webpackChunkName: "demo" */ '../views/Demo.vue')
  },
  {
    path: '/join',
    name: 'JoinUs',
    component: () => import(/* webpackChunkName: "join" */ '../views/JoinUs.vue'),
    props: true,
    meta: {
      auth: "guest"
    }
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import(/* webpackChunkName: "login" */ '../views/Login.vue'),
    props: true,
    meta: {
      auth: "guest"
    }
  },
  {
    path: '/dashboard',
    name: 'UserDashboard',
    component: () => import(/* webpackChunkName: "UserDashboard" */ '../views/UserDashboard.vue'),
    props: true,
    meta: {
      auth: "registered"
    }
  }

]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  scrollBehavior() {
    return {
      x: 0,
      y: 0
    };
  },
})

router.beforeEach((to, from, next) => {
  switch (to.meta.auth) {
    case "registered":
      if (window.$cookies.isKey('token')|Vue.prototype.$session.has('token')) {
        next()
        return
      }else{
        next('/login');
      }
      break;

    case "guest":
      if (!(window.$cookies.isKey('token')|Vue.prototype.$session.has('token'))) {
        next()
        return
      }else{
        next('/dashboard');
      }
      break;
  
    default:
      next()
  }

})

export default router