<template>
  <div class="portofolio">
    <b-container>
      <b-row>
        <b-col>
          <div class="container-title">
            <div class="title-background"></div>
            <div class="title-text">
              <span class="span-lighter">FEATURED</span>&nbsp;
              <span class="span-bolder">WORKS</span>
            </div>
          </div>
        </b-col>
      </b-row>
      <b-row>
        <b-col>
          <div class="row justify-content-center">
        <img class="img-cloud3" :src="require('@/assets/awan2.svg')" alt="">
            <div class="col-md-6 text-center" v-for="(portofolio, index) in portofolios" :key="index">
              <div class="container-f-2">
                <b-img
                  class="image-f-1"
                  fluid
                  :src="url_image + portofolio.photo_product"
                ></b-img>
                <div class="overlay text-left row align-items-center m-0">
                  <div style="color: white;">
                    <h1 style="font-size: 14px; line-height: 19px; letter-spacing: 0.1em;"><strong>{{portofolio.year}}</strong> . CLIENT : <strong>{{portofolio.client.name}}</strong></h1>
                    <h1 style="font-weight: bold; font-size: 24px; line-height: 30px; letter-spacing: 0.02em;">{{portofolio.product_name}}</h1>
                    <h1 style="font-size: 14px; line-height: 24px; letter-spacing: 0.02em;">{{portofolio.description}}</h1>
                    <h1 style="font-style: italic; font-weight: 600; font-size: 12px; line-height: 20px; letter-spacing: 0.04em;">{{portofolio.technologies}}</h1>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "Portofolio",
  data() {
    return {
      index: -1,
      slide: 0,
      portofolios: [],
      url_image: process.env.VUE_APP_API_URL + "/images/portofolios/",
    };
  },
  methods: {
    previous: function() {
      this.slide -= 1;
      if (this.slide == -1) {
        this.slide = this.portofolios.length - 1;
      }
    },
    next: function() {
      this.slide += 1;
      if (this.slide == this.portofolios.length) {
        this.slide = 0;
      }
    },
  },
  mounted() {
    axios
      .get(process.env.VUE_APP_API_URL + "/api/v1/portofolios/previous_work")
      .then((response) => {
        this.portofolios = response.data.prev_work;
      });

    this.$nextTick(function() {
      window.setInterval(() => {
        this.next();
      }, 10000);
    });
  },
};
</script>

