<script>
export default {
  name: "SportProfile",
  data() {
    return {
      gif_url: "",
    };
  },
  async mounted() {
    await this.getShortProfileGif();
  },
  methods: {
    async getShortProfileGif() {
      await this.axios
        .get(process.env.VUE_APP_API_URL + "/api/v1/short-profile")
        .then((response) => {
          this.gif_url =
            process.env.VUE_APP_API_URL +
            "/storage/gif/short-profile/" +
            response.data.value;
        });
    },
    moveDown: function() {
      var container_height =
        document.getElementById("short-profile-box").getBoundingClientRect()
          .height + 40;
      window.scrollTo({
        top: container_height,
        behavior: "smooth",
      });
    },
  },
};
</script>

